te;\n      bottom: 0;\n      left: 0;\n      max-width: 100%;\n      padding: $card-padding;\n    }\n  }\n\n  .card-content {\n    padding: $card-padding;\n    border-radius: 0 0 2px 2px;\n\n    p {\n      margin: 0;\n    }\n    .card-title {\n      display: block;\n      line-height: 32px;\n      margin-bottom: 8px;\n\n      i {\n        line-height: 32px;\n      }\n    }\n  }\n\n  .card-action {\n    &:last-child {\n      border-radius: 0 0 2px 2px;\n    }\n    background-color: inherit; // Use inherit to inherit color classes\n    border-top: 1px solid rgba(160,160,160,.2);\n    position: relative;\n    padding: 16px $card-padding;\n\n    a:not(.btn):not(.btn-large):not(.btn-floating) {\n      color: $card-link-color;\n      margin-right: $card-padding;\n      transition: color .3s ease;\n      text-transform: uppercase;\n\n      &:hover { color: $card-link-color-light; }\n    }\n  }\n\n  .card-reveal {\n    padding: $card-padding;\n    position: absolute;\n    background-color: $card-bg-color;\n    width: 100%;\n    overflow-y: auto;\n    left: 0;\n    top: 100%;\n    height: 100%;\n    z-index: 3;\n    display: none;\n\n    .card-title {\n      cursor: pointer;\n      display: block;\n    }\n  }\n}\n","#toast-container {\n  display:block;\n  position: fixed;\n  z-index: 10000;\n\n  @media #{$small-and-down} {\n    min-width: 100%;\n    bottom: 0%;\n  }\n  @media #{$medium-only} {\n    left: 5%;\n    bottom: 7%;\n    max-width: 90%;\n  }\n  @media #{$large-and-up} {\n    top: 10%;\n    right: 7%;\n    max-width: 86%;\n  }\n}\n\n.toast {\n  @extend .z-depth-1;\n  border-radius: 2px;\n  top: 35px;\n  width: auto;\n  margin-top: 10px;\n  position: relative;\n  max-width:100%;\n  height: auto;\n  min-height: $toast-height;\n  line-height: 1.5em;\n  background-color: $toast-color;\n  padding: 10px 25px;\n  font-size: 1.1rem;\n  font-weight: 300;\n  color: $toast-text-color;\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  cursor: defaul