kground-color: darken($button-raised-background, 10%);\n  }\n}\n\n// Raised Button\n.btn {\n  text-decoration: none;\n  color: $button-raised-color;\n  background-color: $button-raised-background;\n  text-align: center;\n  letter-spacing: .5px;\n  @extend .z-depth-1;\n  transition: background-color .2s ease-out;\n  cursor: pointer;\n  &:hover {\n    background-color: $button-raised-background-hover;\n    @extend .z-depth-1-half;\n  }\n}\n\n// Floating button\n.btn-floating {\n  &:hover {\n    background-color: $button-floating-background-hover;\n    @extend .z-depth-1-half;\n  }\n  &:before {\n    border-radius: 0;\n  }\n  &.btn-large {\n    &.halfway-fab {\n      bottom: -$button-floating-large-size / 2;\n    }\n    width: $button-floating-large-size;\n    height: $button-floating-large-size;\n    padding: 0;\n    i {\n      line-height: $button-floating-large-size;\n    }\n  }\n\n  &.btn-small {\n    &.halfway-fab {\n      bottom: -$button-floating-small-size / 2;\n    }\n    width: $button-floating-small-size;\n    height: $button-floating-small-size;\n    i {\n      line-height: $button-floating-small-size;\n    }\n  }\n\n  &.halfway-fab {\n    &.left {\n      right: auto;\n      left: 24px;\n    }\n    position: absolute;\n    right: 24px;\n    bottom: -$button-floating-size / 2;\n  }\n  display: inline-block;\n  color: $button-floating-color;\n  position: relative;\n  overflow: hidden;\n  z-index: 1;\n  width: $button-floating-size;\n  height: $button-floating-size;\n  line-height: $button-floating-size;\n  padding: 0;\n  background-color: $button-floating-background;\n  border-radius: $button-floating-radius;\n  @extend .z-depth-1;\n  transition: background-color .3s;\n  cursor: pointer;\n  vertical-align: middle;\n  i {\n    width: inherit;\n    display: inline-block;\n    text-align: center;\n    color: $button-floating-color;\n    font-size: $button-large-icon-font-size;\n    line-height: $button-floating-size;\n  }\n}\n\n// button fix\nbutton.btn-floating